@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root{
  --primary: #7bc3de;
  --primary-dark: #336699;
  --secondary: #d8f793;
  --secondary-dark: #a0ca92;
  --dark: #2F4858;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: "Noto Serif";
  background-color: #d8f793;
  background-color: var(--secondary);
  padding: 0;
  margin: 0;
}

#root {
  width: 100%;
}

.App{
  width: 80%;
  margin: auto;
}

.title > h2, .title > p{
  color: var(--primary);
  text-align: center;
}
.title > h2{
  margin-top: 60px;
  font-size: 2.6rem;
}
.progress-bar {
    height: 5px;
    background: var(--primary);
    margin-top: 20px;
}
.upload-form{
    margin: 30px auto 10px;
    text-align: center;
  }
  .upload-form > label input{
    height: 0;
    width: 0;
    opacity: 0;
  }
  .upload-form > label{
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid var(--primary);
    border-radius: 50%;
    margin: 10px auto;
    line-height: 30px;
    color: var(--primary);
    font-weight: bold;
    font-size: 24px;
  }
  .upload-form > label:hover{
    background: var(--primary);
    color: white;
    cursor: pointer;
  }
  .output{
    height: 60px;
    font-size: 0.8rem;
  }
  .error{
    color: var(--error);
  }
.img-grid {
    margin: 20px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
  }
  
  .img-wrap {
    overflow: hidden;
    height: 0;
    padding: 50% 0;
    position: relative;
    opacity: 0.8;
    outline: var(--primary) solid 3px;
    transition: outline 1.5s ease-in;
  }
  
  .img-wrap img {
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    position: absolute;
    top: 0;
    left: 0;
  }
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }
  
  .backdrop img {
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
    border: 3px solid white;
  }
.nav-wrap {
    width: 100%;
    background-color: var(--primary-dark);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 10000;
}

.nav-bar {
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: space-between;
}

.spacer {
    margin: 0 auto;
}

.logo > a {
    text-align: center;
    font-size: large;
    text-decoration: none;
    padding: 1rem;
    color: var(--primary);
    display: inherit;
}

.logo > a:hover {
    cursor: pointer;
}

.nav-bar > ul {
    display: inherit;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.nav-bar > ul > li > a:hover {
    background-color: var(--secondary-dark);
    transition: background-color 275ms ease-in;
}

.nav-bar > ul > li > a {
    display: block;
    color: white;
    text-align: center;
    padding: 1.1rem;
    text-decoration: none;
}

@media only screen and (max-width: 600px) {
    .nav-bar > ul > li > a {
        padding: 1.15em 0.5rem 1em 0.5rem;
    }
    .nav-bar {
        width: 100%;
    }
}

.auth-wrap {
    margin: 20vh auto;
    max-width: 600px;
    padding: 1rem;
}

.auth-wrap > form {
    color: var(--primary);
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.auth-wrap > form > label {
    margin-bottom: 0.6rem;
}

.auth-wrap > form > input {
    border: 1px solid var(--secondary);
    padding: 0.8rem;
    margin-bottom: 0.6rem;
    border-radius: 5px;
    outline: none;
    font-size: medium;
}

.auth-wrap > form > input:focus {
    border: 1px solid var(--primary);
}

.auth-wrap > form > span {
    color: var(--primary);
    font-size: x-large;
    margin: 1rem auto;
}

.button-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.button {
    font-family: "Noto Serif";
    font-weight: 800;
    font-size: medium;
    justify-self: center;
    margin: 0.8rem 0;
    width: 45%;
    padding: 0.5rem;
    border-radius: 5px;
    color: black;
}

.button-primary {
    background-color: var(--primary);
    border: 1px solid var(--primary);
}

.button-secondary {
    background-color: var(--secondary-dark);
    border: 1px solid var(--secondary-dark);
    text-align: center;
    text-decoration: none;
    color: black;
}

.button:hover {
    background-color: #2F4858;
    border-color: #2F4858;
    cursor: pointer;
    color: white;
    transition: background-color 275ms ease-in-out;
}

.error { 
    color: var(--error);
    margin: 0 auto;
    padding: 1rem;
}
.loader {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border: 10px solid var(--primary);
    border-top: 10px solid var(--primary-dark);
    border-radius: 50%;
    width: 75px;
    height: 75px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
    0% { transform: rotate(0deg);}
    100% { transform: rotate(360deg);}
}

@keyframes spin {
    0% { transform: rotate(0deg);}
    100% { transform: rotate(360deg);}
}
.home-wrap {
    display: flex;
    flex-flow: column;
    width: 80%;
    margin: 0 auto;
    padding: 5rem;
}

.card {
    padding: 1rem;
    border-radius: 10px;
    background-color: var(--primary);
    margin: 1rem;
}

.home-image-wrap {
    overflow: hidden;
    border-radius: 10px;
    height: 0;
    padding: 50% 0;
    position: relative;
    box-sizing: border-box;
}

.home-image-wrap > img {
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    position: absolute;
    top: 0;
    left: 0;

    object-fit: contain;
}

.created-by {
    font-family: inherit;
    display: inline-block;
    color: white;
    font-size: larger;
    margin: 1rem;
}

@media only screen and (max-width: 600px) {
    .home-wrap {
        width: 100%;
        padding: 0;
    }
}
